import React, { PropTypes, Component } from 'react'
import withMedia from '../../containers/withmedia';
import {Switch, Route, withRouter} from 'react-router-dom';
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from "react-google-maps";
import { connect } from 'react-redux';
import GuestsList from '../guestslist';
import {rename_restaurant} from '../../store/reducers';
import Edit from 'react-icons/lib/md/edit'
import Directions from '../directions';
import Info from '../info';
import style from './style.css';
import Map from '../map';

class HomeScreen extends Component {

  constructor(props){
    super(props)
    this.state = { show: null, edit_restaurant: false }
    this.show = (title) => { this.setState({...this.state, show: title})}
    this.edit_restaurant = (bool='none') => { this.setState({...this.state, edit_restaurant: bool==='none' ? !this.state.edit_restaurant : bool })}
    this.on_change_restaurant_name = (event) => { 
      let restaurant_name = event.target.value;
      this.setState({...this.state, restaurant_name: restaurant_name })
    }
    this.saveRestaurant = () => {
        this.props.rename_restaurant(this.state.restaurant_name);
        this.edit_restaurant(false);
    }
    this.on_key_press = (event) => {
      if(event.charCode==13 ){
        this.textInput && this.textInput.blur()
      }
    }
    this.onMapMounted = ref => {
        this.map = ref
    }
  }

  

  render() {
    const {restaurant, guests} = this.props
    const is_large = this.props.browser.greaterThan.small
    const mapCenter = restaurant ? { lng: restaurant.lng, lat: restaurant.lat } : null
    const bounds = new window.google.maps.LatLngBounds();
    guests && guests.forEach((guest) => {
      bounds.extend({lng: guest.lng, lat: guest.lat})
    })
    const guests_markers = guests ? guests.map((guest) =>
        <Marker position={ {lat: parseFloat(guest.lat), lng: parseFloat(guest.lng)} } />
      ) : []
    const restaurant_marker = restaurant ? [<Marker position={ {lat: parseFloat(restaurant.lat), lng: parseFloat(restaurant.lng)} } />] : []
    const markers = [...guests_markers, ...restaurant_marker ]
    this.map && this.map.fitBounds(bounds)
    this.map && this.map.panToBounds(bounds)
    // this.map && this.map.getBounds() && this.map.setZoom(this.map.getBounds().getZoom())
    return (
      <div className={ style.container }>
        <Info showLink={ !!restaurant } linkTitle="change restaurant" onClickFunc={ () => { this.props.history.push(`/lunchtime/change-restaurant`)} }>
            <h3 onClick={ () => {  this.edit_restaurant(true) }} className={ style.title }>
              { this.state.edit_restaurant 
                  ? <div><input type="text" ref={ (r) => { this.textInput = r; r && r.focus(); } } onKeyPress={ this.on_key_press } onChange={(e) => { this.on_change_restaurant_name(e) }} onBlur={ () => { this.saveRestaurant() } } value={ this.state.restaurant_name }/> <span className={ style.smallLink } onClick={() => { this.edit_restaurant(false) }}>save...</span></div>
                  : ( restaurant && restaurant.name ? restaurant.name : "Restaurant Name" )
              }
            </h3> 
            { restaurant && restaurant.address
                ? <span>{ restaurant.address }</span>
                : <a className={ style.link } onClick={() => { this.props.history.push(`/lunchtime/change-restaurant`) }}>Add Restaurant</a> 
            }
          </Info>
          <Info title="Guests" linkTitle="add guest" showLink={guests && guests.length < 4} onClickFunc={ () => { if(guests && guests.length < 4){ this.props.history.push(`/lunchtime/add-guest`) } } }>
            { guests 
                ? guests.map((guest, key) => <Directions idx={ key } key={`guest_${key}`} guest={ guest } />)
                : <a  className={ style.link } onClick={() => { this.props.history.push(`/lunchtime/add-guest`) }}>Add Guest</a> }
          </Info>

          { restaurant &&
            <div id="map">
              <Map
                loadingElement={<div style={{ height: `100%` }} />}
                containerElement={<div style={{ height: `400px` }} />}
                mapElement={<div style={{ height: `100%` }} />}
                onMapMounted={this.onMapMounted}
                center={ mapCenter }
                markers={ markers }
              />
            </div>
          }
      </div>
    )
  }
}

export default withRouter(withMedia(connect( state => ({restaurant: state.app.restaurant, guests: state.app.guests}), dispatch => ({ rename_restaurant: (payload) => { dispatch(rename_restaurant(payload))} }))(HomeScreen)))



