import React, { PropTypes, Component } from 'react'
import {connect} from 'react-redux';
// const { compose, withProps, lifecycle } = require("recompose");
import { withScriptjs, withGoogleMap, GoogleMap, DirectionsRenderer } from "react-google-maps"
import Bus from 'react-icons/lib/md/directions-bus'
import Car from 'react-icons/lib/md/directions-car'

import style from './style.css'


class Directions extends Component {

  constructor(props){
    super(props)
    this.state = { DRIVING: null, TRANSIT: null }
    this.getDirection = this.getDirection.bind(this)
  }

  componentDidMount() {
    setTimeout(this.getDirection('DRIVING'), 2000)
    setTimeout(this.getDirection('TRANSIT'), 2500)
  }

  getDirection(mode) {
    const { guest, restaurant } = this.props
    const DirectionsService = new window.google.maps.DirectionsService();
    if(guest && restaurant && guest.lat && guest.lng && restaurant.lat && restaurant.lng){
      DirectionsService.route({
        origin: new window.google.maps.LatLng(guest.lat, guest.lng),
        destination: new window.google.maps.LatLng(restaurant.lat, restaurant.lng),
        travelMode: window.google.maps.TravelMode[mode],
      }, (result, status) => {
        if (status === window.google.maps.DirectionsStatus.OK) {
          let info = result.routes[0].legs[0]
          // console.log(`directions ${JSON.stringify(info)}`);
          this.setState({
            [mode]: info,
          });
        } else {
          console.error(`error fetching directions ${result}`);
        }
      });
    }
  }

  render() {
    const { guest } = this.props
    const { duration: dduration, distance: ddistance } = this.state.DRIVING ? this.state.DRIVING : {}
    const { duration: tduration, distance: tdistance } = this.state.TRANSIT ? this.state.TRANSIT : {}
    console.log(this.state)
    return (
      <div className={ style.row }>
        <div className={ style.title }>
          <h4>{ guest.name || `Guest ${this.props.idx + 1}` }</h4>
          { guest.address }
        </div>
        <div className={ style.info }>
          <div> <Car/> by car: </div>
          <div> <Bus/> by bus: </div>
        </div>
        <div className={ style.info }>
          <div> { ddistance && ddistance.text} </div>
          <div> { tdistance && tdistance.text} </div>
        </div>
        <div className={ style.info }>
          <div> { dduration && dduration.text} </div>
          <div> { tduration && tduration.text} </div>
        </div>
      </div>
    )
  }
}

export default connect( 
    state =>({
      restaurant: state.app.restaurant
    }),
    dispatch => { 
  return { 
  } 
    }
)(Directions);



