
import React, { Component } from 'react'
import {connect} from 'react-redux';
import {
  withScriptjs,
  withGoogleMap,
  GoogleMap,
  Marker,
} from "react-google-maps";

const MapWithRestaurant = withGoogleMap(props => {
  const { markers, guests, restaurant } = props
  return(
    <GoogleMap 
      ref={props.onMapMounted}
      defaultZoom={10}
      center={props.center}
      onBoundsChanged={props.onBoundsChanged}
    >
      { markers }
    </GoogleMap>
)
}
);

export default connect( state => { return { restaurant: state.restaurant, guests: state.guests }},{})(MapWithRestaurant)



